* {
  box-sizing: border-box;
}

body {
  --hover-color: #00acdc;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  display: flex;
}

.logo {
  margin-left: 50px;
  display: inline-flex;
}

.logo_header {
  width: 40px;
  height: 40px;
}

.nav_header {
  margin-right: 50px;
}

.header_nav_list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_link {
  text-transform: uppercase;
  color: #000;
  height: 100%;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.header_nav_item:nth-child(n+2) {
  margin-left: 20px;
}

.nav_link:hover {
  color: var(--hover-color);
}

.nav_link:active {
  content: "";
}

.is-active {
  color: var(--hover-color);
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  top: 56px;
}
